import React from 'react'
import {PopoutContactElements} from "../../components"
import MetaDecorator from "../MetaDecorator";
import contactsData from "../../data/contactsData";

const ContactsPage = () => {
    const contactHader = "Контактная информация"
    const contactsNames = contactsData.contacts.map(
        (contact, index) => contact.name)

    return <>
        <h3 style={{textAlign: "center"}}>{contactHader}</h3>
        <MetaDecorator title={contactHader} description={contactsNames}/>
        <PopoutContactElements/>
    </>
}

export default ContactsPage
